/*
Theme Name: isif-life
Theme URI: http://gurusite.ru
Description: html 5, 2-column blog theme
Version: 2.0
Author: Artem Mironov
Author URI: http://gurusite.ru
*/

* {
  margin: 0;
  padding: 0;
}

html {
  background: #252b3a url(../img/body_bg.jpg) repeat;
}

body {
  font: 12px/17px Verdana, Arial, Helvetica, sans-serif;
  background: url(../img/2.jpg) no-repeat center top;
}

a {
  &:link, &:visited {
    text-decoration: underline;
    color: #0033CC;
    border: 0;
    -moz-outline-style: none;
  }

  &:hover {
    text-decoration: underline;
    color: #ff7200;
    border: 0;
    -moz-outline-style: none;
  }

  &:active {
    text-decoration: none;
    color: #ff7200;
    border: 0;
    -moz-outline-style: none;
  }
}

.underline {
  text-decoration: underline;
}

p {
  margin: 10px 0;
}

h1, h2, h3, h4 {
  padding: 5px 0 10px;
  letter-spacing: -1px;
  font-family: Georgia, Helvetica, sans-serif;
  color: #000001;
}

h1 {
  font-size: 24px;
  font-weight: normal;
}

h2 {
  font-size: 22px;
  font-weight: normal;
}

h3 {
  font-size: 18px;
  font-weight: bold;
}

h4 {
  font-size: 16px;
  font-weight: bold;
}

.clear {
  clear: both;
}

hr {
  height: 1px;
  border-top: 1px solid #CFCFCF;
}

blockquote, code {}

blockquote {
  position: relative;
  padding: 15px 22px 12px 25px;
  margin: 16px 0 19px;
  background: #f3f3f3;
  border: 1px solid #dfdfdf;
  border-left: 2px solid #3965a8;
  font: italic normal 14px Arial, sans-serif;
  color: #405778;

  &:before, &:after {
    content: "";
    position: absolute;
    z-index: -1;
  }

  &:before {
    top: 50%;
    bottom: 0px;
    left: 100px;
    right: 100px;
    -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 1);
    -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 1);
    box-shadow: 0 0 10px rgba(0, 0, 0, 1);
    -moz-border-radius: 120px / 10px;
    border-radius: 120px / 10px;
  }
}

#wrapper {
  position: relative;
  width: 980px;
  margin: 0 auto;
}

/*----------------------------------- Header ------------------------------------*/

header {
  display: block;
  width: 980px;
  height: 336px;
}

h1.blog_name, .head_name {
  width: 340px;
  padding: 145px 0 0 55px;
  text-shadow: 2px 2px 2px #3d3d3d;
  word-spacing: 4px;
  font: 700 33px/36px Arial, sans-serif;
  letter-spacing: -1px;
}

h1.blog_name a, .head_name a {
  color: #fffffe;
  text-decoration: none;
}

h2.blog_descr, .blog_descr {
  width: 280px;
  padding: 5px 0 0 55px;
  text-shadow: 2px 2px 2px #3d3d3d;
  letter-spacing: 1px;
  font: normal 16px Tahoma, Arial, sans-serif;
  color: #fffffe;
}

/*-------------- Up menu ---------------*/

nav {
  display: block;
  width: 980px;
  height: 126px;
}

a.ribbon {
  display: block;
  width: 235px;
  height: 59px;
  margin-bottom: -1px;
  background: url(../img/ribbon.png) no-repeat 0 0;
}

#access {
  width: 963px;
  height: 69px;
  margin: 0 auto;
  padding-left: 17px;
  background: #ffffff url(../img/menu_top.jpg) no-repeat 0 0;

  ul {
    list-style: none;
    margin: 0;

    li {
      float: left;
      position: relative;
      height: 69px;
      margin: -1px 2px 0 0;

      &:hover, &.current_page_item, &.current-menu-ancestor, &.current-menu-item, &.current-menu-parent {
        background: url(../img/menu_bottom.jpg) no-repeat center bottom;
      }

      a {
        display: block;
        padding: 0 13px;
        margin-top: 2px;
        text-decoration: none;
        font: normal 15px/59px Tahoma, sans-serif;
        color: #ffffff;
      }

      &.menu-item-first a {
        border-bottom: 3px solid #f12d00;
      }

      &.menu-item-8358 a {
        border-bottom: 3px solid #ff5d0b;
      }

      &.menu-item-5758 a {
        border-bottom: 3px solid #ffba00;
      }

      &.menu-item-6215 a {
        border-bottom: 3px solid #8dc63f;
      }

      &.menu-item-2811 a {
        border-bottom: 3px solid #ff1533;
      }

      &.menu-item-8294 a {
        border-bottom: 3px solid #ff5d0b;
      }

      &.menu-item-2813 a {
        border-bottom: 3px solid #1067a0;
      }

      &.menu-item-2814 a {
        border-bottom: 3px solid #662d91;
      }

      &.menu-item-6437 a {
        border-bottom: 3px solid #ff5d0b;
      }

      &.menu-item-last a {
        border-bottom: 3px solid #ffba00;
      }

      &.menu-item-2816 a {
        border-bottom: 3px solid #ffffff;
      }

      a:hover {
        margin-top: 0px;
        font: normal 15px/59px Tahoma, sans-serif;
        text-decoration: none;
        background: url(../img/menu_hover.png) repeat-x;
        box-shadow: 0 0 1px #ffffff inset, 0px 0px 3px #000003;
      }

      &.menu-item-first a:hover {
        border-top: 2px solid #f12d00;
      }

      &.menu-item-8358 a:hover {
        border-top: 2px solid #ff5d0b;
      }

      &.menu-item-5758 a:hover {
        border-top: 2px solid #ffba00;
      }

      &.menu-item-6215 a:hover {
        border-top: 2px solid #8dc63f;
      }

      &.menu-item-2811 a:hover {
        border-top: 2px solid #ff1533;
      }

      &.menu-item-8294 a:hover {
        border-top: 2px solid #ff5d0b;
      }

      &.menu-item-2813 a:hover {
        border-top: 2px solid #1067a0;
      }

      &.menu-item-2814 a:hover {
        border-top: 2px solid #662d91;
      }

      &.menu-item-6437 a:hover {
        border-top: 2px solid #ff5d0b;
      }

      &.menu-item-last a:hover {
        border-top: 2px solid #ffba00;
      }

      &.menu-item-2816 a:hover {
        border-top: 2px solid #ffffff;
      }

      &.current_page_item > a, &.current-menu-ancestor > a, &.current-menu-item > a, &.current-menu-parent > a {
        background: url(../img/menu_current.png) repeat-x;
        box-shadow: 0 0 1px #ffffff inset, 1px 0px 1px #000003 inset;
        text-decoration: none;
        color: #868686;
      }
    }
  }
}

* html #access ul li {
  &.current_page_item a, &.current-menu-ancestor a, &.current-menu-item a, &.current-menu-parent a {
    background: url(../img/menu_current.png) repeat-x;
    box-shadow: 0 0 1px #ffffff inset, 1px 0px 1px #000003 inset;
    text-decoration: none;
    color: #868686;
  }
}

/*----------------------------------- Content -----------------------------------*/

#container {
  width: 100%;
  background: url(../img/container.jpg) repeat-y;

  &:after {
    content: '';
    display: block;
    clear: both;
    visibility: hidden;
    height: 0;
  }
}

.top-comment:after, .post_bar:after, .comment-wrap:after, form#commentform:after, #sidebar-fixed:after, .fixed_wrapper:after {
  content: '';
  display: block;
  clear: both;
  visibility: hidden;
  height: 0;
}

#content {
  float: left;
  width: 615px;
  padding-top: 15px;
  font: 12px/17px Verdana, Arial, Helvetica, sans-serif;

  .pageintro {
    position: relative;
    float: left;
    width: 605px;
    left: 35px;
  }
}

/*------------------------- Post ------------------------------*/

h1#pagetitle {
  padding: 0;
  margin: 0;
  font: normal 24px/28px Arial, sans-serif;
  color: #0099FF;
  visibility: hidden;
}

.postwrap {
  position: relative;
  width: 583px;
  margin: 0 0 20px 32px;
  font: normal 12px/17px Verdana, sans-serif;
  z-index: 0;

  img {
    max-width: 100%;
    height: auto;
    margin: 15px 0 19px;

    /*box-shadow: 0 0 7px 0 #333334, 0 10px 15px -7px rgba(0,0,0,1);*/
  }
}

.wp-caption {
  max-width: 100%;
  height: auto;
  margin: -10px 0 -13px;
}

.wp-caption-text {
  display: none;
}

.postwrap {
  p {
    margin: 10px 0;
  }

  ul, ol {
    margin: 15px 0 15px 25px;
  }

  li {
    list-style-type: circle;
    margin-bottom: 4px;
  }

  ul.children, ol.children {
    margin-left: 30px;
  }

  ul.speclist, ol.specnumber {
    margin: 15px 0;
    counter-reset: point;
  }

  ul.speclist li, ol.specnumber li {
    position: relative;
    margin: 5px 0;
    padding: 0 0 0 25px;
    list-style-type: none;
  }

  ul.speclist li {
    margin-left: 2px;
  }

  ol.specnumber li {
    margin: 7px;
    padding: 7px 0 0 35px;

    ul.speclist li {
      margin: 5px 0;
      padding: 0 0 0 25px;
      font: normal 12px/17.5px Verdana, sans-serif;
    }
  }

  ul.speclist li ol.specnumber li {
    padding-left: 35px;
    font: normal 12px/17.5px Verdana, sans-serif;
  }
}

span.borbottom {
  border-bottom: 1px dashed #000002;
}

.postwrap {
  ul.speclist li:before {
    position: absolute;
    width: 20px;
    height: 20px;
    top: 0;
    left: 0;
    content: '';
    background: url(../img/speclist.jpg) no-repeat;
  }

  ol.specnumber li {
    ul.speclist li:before {
      position: absolute;
      width: 20px;
      height: 20px;
      top: 0;
      left: 0;
      content: '';
      background: url(../img/speclist.jpg) no-repeat;
    }

    &:before {
      position: absolute;
      width: 30px;
      height: 30px;
      top: 0;
      left: 0;
      background: url(../img/specnumber.jpg) no-repeat;
      content: counter(point);
      counter-increment: point 1;
      text-align: center;
      font: normal 12px/32px Arial, sans-serif;
      color: #fffffe;
    }
  }

  ul.speclist li ol.specnumber li:before {
    position: absolute;
    width: 30px;
    height: 30px;
    top: 0;
    left: 0;
    background: url(../img/specnumber.jpg) no-repeat;
    content: counter(point);
    counter-increment: point 1;
    text-align: center;
    font: normal 12px/32px Arial, sans-serif;
    color: #fffffe;
  }
}

h1.title {
  padding: 0;
  margin: 0;
  text-decoration: none;

  /*text-shadow: 2px 2px 8px #010101;*/
  font: normal 24px/28px Arial, sans-serif;
  color: #000001;
}

h2.title {
  a {
    padding: 0;
    margin: 0;
    text-decoration: none;

    /*text-shadow: 2px 2px 8px #010101;*/
    font: normal 24px/28px Arial, sans-serif;
    color: #000001;

    &:hover {
      text-decoration: none;
      color: #0066FF;
    }
  }

  padding: 0;
}

.post_bar {
  width: 583px;
  margin: 10px 0 10px;
  font: normal 11px Arial, sans-serif;
  color: #797979;
}

.post_author, .post_date {
  float: left;
}

.blue {
  text-decoration: underline;
  color: #000000;
}

.post_count {
  float: right;
}

.post_info {
  width: 583px;
  height: 60px;
  padding: 0 0 0 32px;
  margin: 30px 0 35px;
  background: #ededed;
  border-top: 1px dashed #898989;
  font: 11px Georgia, "Times New Roman", Times, serif;
}

.post_comments a {
  float: left;
  width: 184px;
  height: 37px;
  margin: 11px 12px 0 0;
  background: url(../img/post_comment.jpg) no-repeat 0 0;
  text-align: center;
  text-decoration: none;
  font: bold 14px/37px Arial, sans-serif;
  color: #0169D3;

  &:hover {
    background: url(../img/post_comment.jpg) no-repeat 0 -37px;
    color: #FF7200;
  }

  &:active {
    background: url(../img/post_comment.jpg) no-repeat 0 -74px;
    color: #FF7200;
  }
}

.post_comments_count {
  float: left;
  font: normal 12px/60px Arial, sans-serif;
  color: #797979;
}

.com_count {
  text-decoration: underline;
  font-size: 15px;
  color: #000000;
}

.post_cat {
  float: right;
  margin-right: 10px;
  font: normal 12px/60px Arial, sans-serif;
  color: #797979;
}

.cat_name {
  font-size: 15px;
}

.more-link {
  display: block;
  width: 160px;
  padding: 4px 10px;
  margin-top: 10px;
  background: #e1eef7;
  text-align: center;
  font: normal 12px Arial, sans-serif;

  &:hover {
    background: #ececec;
  }
}

.single_feed {
  position: relative;
  width: 647px;
  height: 282px;
  padding-top: 40px;
  margin: 0 -32px 0 0;
  background: url(../img/single_feed.jpg) no-repeat;
}

.feed_text {
  width: 350px;
  height: 100px;
  margin: 0 0 0 255px;
  letter-spacing: -1px;
  text-shadow: 0.1px 0.1px 0.1px #ed1c21;
  font: normal 33px/32px Arial, sans-serif;
  color: #ed1c24;
}

.bold {
  font-weight: bold;
}

.feed_friends {
  position: absolute;
  bottom: 10px;
  right: 52px;
  font: 17px Arial, sans-serif;
  color: #2d2e32;
}

.feed_form {
  width: 350px;
  height: 37px;
  margin: 10px 0 0 255px;
}

.feed_mail {
  float: left;
  width: 150px;
  height: 28px;
  padding-left: 7px;
  background: #ededed;
  border: none;
  box-shadow: 1px 1px 1px #aaaaad inset;
  font: italic normal 13px Arial, sans-serif;
  color: #666665;

  &:focus {
    outline: 0;
  }
}

.feed_submit {
  float: left;
  width: 155px;
  height: 37px;
  margin: -4px 0 0 -1px;
  background: url(../img/feed_submit.jpg) no-repeat 0 0;
  border: none;
  text-align: center;
  font: normal 14px/37px Arial, sans-serif;
  color: #0169D3;
  cursor: pointer;

  &:hover {
    background: url(../img/feed_submit.jpg) no-repeat 0 -37px;
  }

  &:active {
    background: url(../img/feed_submit.jpg) no-repeat 0 -74px;
  }
}

.post_social {
  position: relative;
  height: 54px;
  padding: 0 20px 0 52px;
  margin-bottom: 30px;
}

.twitter_social, .gplus_social, .fb_social {
  position: relative;
  float: left;
  width: 128px;
  height: 54px;
  margin-right: 10px;
  overflow: hidden;
}

.vk_social {
  position: relative;
  float: left;
  width: 128px;
  height: 54px;
  margin-right: 10px;
  overflow: hidden;
  margin-right: 0;
}

/*------------------------------------ Sidebar ------------------------------------------*/

#sidebar {
  float: right;
  position: relative;
  width: 332px;
}

.sidebar_ads {
  .widget {
    width: 240px;
    height: 400px;
    margin: 0 auto 20px;
  }

  .h2 {
    display: none;
  }
}

.sidebar-link, #sidebar-fixed {
  width: 310px;
  margin: 0px auto 15px;
}

.sidebar-link .widget, #sidebar-fixed .widget {
  display: block;
  padding: 0 0 30px;
  margin-bottom: 20px;
  background: #ffffff;
  border: 1px solid #c9c9c9;
  box-shadow: 0 2px 6px #ababab;
}

.sidebar-link .h2, #sidebar-fixed .h2 {
  width: 270px;
  height: 40px;
  padding: 0 20px;
  margin: -1px 0 16px -1px;
  background: url(../img/sidebar_h2.jpg) repeat-x;
  font: bold 16px/40px Arial, sans-serif;
  color: #2d2e32;
}

.fixed {
  position: fixed;
  top: 0px;
}

.fixed_wrapper {
  position: relative;
  width: 308px;
}

#fixed_right {
  width: 308px;
  top: 0px;
  margin-top: 10px;
  text-align: center;
}

/*---------- Google search ----------*/

#cse-search-box {
  width: 330px;
  height: 55px;
  padding: 25px 0 0 2px;
  margin-bottom: 20px;
  background: #ffffff;
}

.search_field {
  float: left;
  width: 233px;
  height: 28px;
  padding-left: 7px;
  background: #ededed;
  border: none;
  box-shadow: -1px -1px 1px #aaaaad;
  font: italic normal 12px Arial, sans-serif;
  color: #afafaf;

  &:focus {
    outline: 0;
  }
}

.search_submit {
  float: left;
  width: 75px;
  height: 36px;
  margin: -4px 0 0 -2px;
  border: none;
  background: url(../img/search.jpg) no-repeat 0 0;
  text-align: center;
  font: bold 14px Arial, sans-serif;
  color: #0169D3;
  cursor: pointer;

  &:hover {
    background: url(../img/search.jpg) no-repeat 0 -37px;
    color: #ff7200;
  }

  &:active {
    background: url(../img/search.jpg) no-repeat 0 -74px;
  }
}

/*---------- Feedburner bar ----------*/

.feedburner {
  position: relative;
  width: 332px;
  height: 325px;
  padding-top: 185px;
  margin-bottom: 20px;
  background: url(../img/feedburner.jpg) no-repeat top right;
  overflow: hidden;
}

.burner_form {
  width: 250px;
  height: 108px;
  padding: 13px 0 0px;
  margin: 0 0px 14px 30px;
  border-bottom: 1px dashed #8c8c8c;
  text-align: center;

  p {
    margin: 0 0 8px;
    text-align: left;
    font: italic normal 12px Arial, sans-serif;
    color: #666665;
  }
}

.burner_mail {
  width: 243px;
  height: 30px;
  padding-left: 7px;
  margin-bottom: 8px;
  background: #ededed;
  border: none;
  box-shadow: -1px -1px 1px #aaaaad;
  color: #afafaf;

  &:focus {
    outline: 0;
  }
}

.burner_submit {
  width: 202px;
  height: 29px;
  margin: 0 auto;
  background: #cccccc url(../img/burner_submit.jpg) no-repeat;
  border: 1px solid #fffffd;
  box-shadow: 1px 1px 5px #aaaaac;
  font: bold 14px Arial, sans-serif;
  color: #0169D3;
  cursor: pointer;

  &:hover {
    box-shadow: 0px 0px 3px #aaaaac;
  }
}

.burner_rss {
  float: left;
  width: 125px;
  height: 35px;
  padding-top: 5px;
  margin: 0 0 0 29px;
  font: bold 11px Arial, sans-serif;
  color: #ff5300;
  margin-bottom: 40px;
}

.burner_twit {
  float: left;
  width: 125px;
  height: 35px;
  padding-top: 5px;
  margin: 0 0 0 8px;
  font: bold 11px Arial, sans-serif;
  color: #0061b4;
}

.burner_icon {
  float: left;
  width: 38px;
  height: 38px;
  margin: -5px 5px 0 0;
  box-shadow: 1px 1px 5px #444441;

  &:hover {
    box-shadow: 1px 1px 2px #444441;
  }
}

.grey_count {
  font: normal 11px Arial, sans-serif;
  color: #626262;
}

.feedburner_info {
  position: absolute;
  width: 125px;
  height: 120px;
  padding: 0 22px 0 7px;
  bottom: 0;
  right: 0;
  background: url(../img/fstar.jpg) no-repeat 0 3px;
  font: normal 11px Arial, sans-serif;
  color: #666665;
}

/*---------- How blog begin ----------*/

a.how_blog {
  display: block;
  width: 266px;
  height: 72px;
  margin: 20px auto;
  background: url(../img/howblog.jpg) no-repeat 0 0;
  text-align: center;
  text-decoration: none;
  font: bold 16px/72px Arial, sans-serif;
  color: #0061b4;

  &:hover {
    background: url(../img/howblog.jpg) no-repeat 0 -72px;
    color: #2d2e32;
  }
}

/*---------- Tabber-tabs bar ----------*/

.box {
  width: 308px;
  padding: 8px 0;
  margin: 0 auto 20px;
  background: #ffffff;
  border: 1px solid #c9c9c9;
  border-top: 5px solid #ffba00;
  box-shadow: 0 2px 6px #ababab;
}

.center {
  text-align: center;
}

ul.category {
  position: relative;
  float: left;
  width: 151px;
  padding-left: 2px;
  overflow: hidden;

  li {
    width: 150px;
    padding: 6px 0;
    margin: 0 0 4px 0;
    cursor: pointer;

    &:hover {
      background: #f4f4f4;
    }

    a {
      font: bold 13px Arial, sans-serif;
      color: #0169D3;
    }

    &:hover a {
      color: #ff7200;
    }
  }
}

.cat_1, .cat_2, .cat_3, .cat_4, .cat_5, .cat_6, .cat_7, .cat_8, .cat_9, .cat_10, .cat_11 {
  float: left;
  width: 16px;
  height: 16px;
  margin: 0 5px 0 10px;
}

.cat_1 {
  background: url(../img/cat_icons.png) no-repeat 0 0;
}

ul.category li:hover .cat_1 {
  background: url(../img/cat_icons.png) no-repeat 0 -16px;
}

.cat_2 {
  background: url(../img/cat_icons.png) no-repeat -16px 0;
}

ul.category li:hover .cat_2 {
  background: url(../img/cat_icons.png) no-repeat -16px -16px;
}

.cat_3 {
  background: url(../img/cat_icons.png) no-repeat -31px 0;
}

ul.category li:hover .cat_3 {
  background: url(../img/cat_icons.png) no-repeat -31px -16px;
}

.cat_4 {
  background: url(../img/cat_icons.png) no-repeat -47px 0;
