/*--------------------------- One comment --------------------------------*/

li.comment .comment-wrap {
  position: relative;
  margin: 0 0 20px;
  padding: 15px 0;
}

.commentlist li {
  &.even {
    .comment-wrap {
      background: #f5f6f8;
      box-shadow: 1px 1px 1px #d3d3d3 inset;
    }

    .children li.comment {
      .comment-wrap {
        background: #ffffff;
        box-shadow: none;
      }

      .children li.comment {
        .comment-wrap {
          background: #f5f6f8;
          box-shadow: 1px 1px 1px #d3d3d3 inset;
        }

        .children li.comment {
          .comment-wrap {
            background: #ffffff;
            box-shadow: none;
          }

          .children li.comment .comment-wrap {
            background: #f5f6f8;
            box-shadow: 1px 1px 1px #d3d3d3 inset;
          }
        }
      }
    }
  }

  &.odd {
    .comment-wrap {
      background: #ffffff;
      box-shadow: none;
    }

    .children li.comment {
      .comment-wrap {
        background: #f5f6f8;
        box-shadow: 1px 1px 1px #d3d3d3 inset;
      }

      .children li.comment {
        .comment-wrap {
          background: #ffffff;
          box-shadow: none;
        }

        .children li.comment {
          .comment-wrap {
            background: #f5f6f8;
            box-shadow: 1px 1px 1px #d3d3d3 inset;
          }

          .children li.comment .comment-wrap {
            background: #ffffff;
            box-shadow: none;
          }
        }
      }
    }
  }

  &.even {
    .comment-wrap.administrator {
      margin: 20px 0;
      background: #DBDDEB;
      box-shadow: 1px 1px 1px #d3d3d3 inset;
      color: #333333;
    }

    .children li.comment {
      .comment-wrap.administrator, .children li.comment .comment-wrap.administrator {
        margin: 20px 0;
        background: #DBDDEB;
        box-shadow: 1px 1px 1px #d3d3d3 inset;
        color: #333333;
      }
    }
  }

  &.odd {
    .children li.comment .children li.comment .children li.comment {
      .comment-wrap.administrator, .children li.comment .comment-wrap.administrator {
        margin: 20px 0;
        background: #DBDDEB;
        box-shadow: 1px 1px 1px #d3d3d3 inset;
        color: #333333;
      }
    }

    .comment-wrap.administrator {
      margin: 20px 0;
      background: #DBDDEB;
      box-shadow: 1px 1px 1px #d3d3d3 inset;
      color: #333333;
    }

    .children li.comment {
      .comment-wrap.administrator {
        margin: 20px 0;
        background: #DBDDEB;
        box-shadow: 1px 1px 1px #d3d3d3 inset;
        color: #333333;
      }

      .children li.comment {
        .comment-wrap.administrator {
          margin: 20px 0;
          background: #DBDDEB;
          box-shadow: 1px 1px 1px #d3d3d3 inset;
          color: #333333;
        }

        .children li.comment {
          .comment-wrap.administrator, .children li.comment .comment-wrap.administrator {
            margin: 20px 0;
            background: #DBDDEB;
            box-shadow: 1px 1px 1px #d3d3d3 inset;
            color: #333333;
          }
        }
      }
    }
  }
}

