        position: static;
        color: #4a515d;
      }
    }
  }

  iframe {
    max-width: 100%;
    height: 50vw;
  }
}

@media screen and (max-width: 767px) {
  #header .slogan {
    display: none;
  }

  .search-form {
    float: none;
    position: static;
    display: inline-block;
  }

  #footer .subscribe {
    float: none;
    margin: 20px 0 0 0;
  }

  .burner_form {
    width: 90%;
    margin: auto;
    padding: 0 1% 100px;
    float: none;
    display: block;
    position: relative;
  }

  footer .copyright {
    width: auto;
    top: 90px;
    margin: 0 10px;
    padding: 20px 0 0 40px;
    position: absolute;
  }

  .form-control {
    width: calc(100% - 60px);
    margin: 0;
  }

  .btn-warning {
    position: absolute;
    right: 0;
  }

  .burner_form:before {
    margin: 20px 0;
  }
}

@media screen and (max-width: 550px) {
  .search-form {
    margin: 10px 5px 0 5px;
    display: block;
  }

  #header .logo {
    margin: 0;
    float: none;

    a {
      padding: 12px 0 12px 54px;
    }
  }

  .slicknav_btn {
    top: -90px;
  }

  .search-form {
    width: 97%;
  }

  #cse-search-box {
    width: 100%;
  }
}

@media screen and (max-width: 380px) {
  #header {
    padding: 3px 0 10px;

    .logo a {
      width: auto;
      max-width: 220px;
    }
  }

  .slicknav_btn {
    top: -100px;
  }

  .postwrap {
    margin: 20px 20px 0;
  }

  .post_info {
    margin: 0 20px 15px;
  }
}
img.wp-smiley,
img.emoji {
  display: inline !important;
  border: none !important;
  box-shadow: none !important;
  height: 1em !important;
  width: 1em !important;
  margin: 0 .07em !important;
  vertical-align: -0.1em !important;
  background: none !important;
  padding: 0 !important;
}
